import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFilm, faVideo } from "@fortawesome/free-solid-svg-icons";

export class Page0 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videoContainerClicked: false,
      personContainerClicked: false,
      readMoreClicked: false,
      containerClicked: false,
    };
    this.handleVideoContainerClick = this.handleVideoContainerClick.bind(this);
    this.handlePersonContainerClick = this.handlePersonContainerClick.bind(this);
    this.handlePersonCloseClick = this.handlePersonCloseClick.bind(this);

    this.readMoreClicked = this.readMoreClicked.bind(this);
  }

  handleVideoContainerClick() {
    let currentState = this.state.videoContainerClicked;
    this.setState({
      videoContainerClicked: !currentState,
    });
  }

  handlePersonContainerClick() {
    let currentState = this.state.personContainerClicked;
    this.setState({
      personContainerClicked: !currentState,
    });
  }

  readMoreClicked() {
    let readMoreButtonCurrentState = this.state.readMoreClicked;
    this.setState({
      readMoreClicked: !readMoreButtonCurrentState,
    });
  }

  handlePersonCloseClick() {
    this.setState({
      containerClicked: false,
    });
  }

  render() {
    let videoContainerClassName = this.state.videoContainerClicked ? "window-opened" : "";
    let personContainerClassName = this.state.personContainerClicked ? "window-opened" : "";

    let personImageNumbers = [1, 2, 3, 4, 1];

    let windowClassName = this.state.readMoreClicked ? "info-window-clicked" : "";

    return (
      <div className="info-containers info-containers-person">
        <div className="background-image"></div>
        <div className="container-zones">
          <div
            className="container-left container author-info"
            onClick={this.handlePersonContainerClick}
          >
            <div className="author-photo media"></div>
            <div className="author-text text">
              <h1>Eugenija imknait</h1>
              <h1>"iniuon"</h1>
            </div>
          </div>
          <div
            className="container-right container video-info"
            onClick={this.handleVideoContainerClick}
          >
            <div className="video media"></div>
            <div className="icons">
              <FontAwesomeIcon className="icon" icon={faVideo} />
              <FontAwesomeIcon className="icon" icon={faFilm} />
            </div>
          </div>
        </div>
        <div
          className={"video-window window " + videoContainerClassName}
          onClick={this.handleVideoContainerClick}
        >
          <div className="window-content">
            <div className="video-media"></div>
          </div>
        </div>
        <div className={"person-window window " + personContainerClassName}>
          <div className={"window-content " + windowClassName}>
            <div className="image-container person-image-container">
              <div className="image-carousel person-image-carousel">
                {personImageNumbers.map((personImageNumbers, index) => {
                  let imageUrl = require(`../images/simkunaite/simkunaite${personImageNumbers}.jpg`);
                  return (
                    <div
                      key={"person-image" + index}
                      style={{
                        backgroundImage: `url(${imageUrl})`,
                      }}
                      className="image person-image"
                    ></div>
                  );
                })}
              </div>
            </div>
            <div className="text-container">
              <div className="btn btn-close" onClick={this.handlePersonContainerClick}>
                <div className="line-container">
                  <div className="line"></div>
                  <div className="line"></div>
                </div>
              </div>
              <h1>Eugenija imknait</h1>
              <p>
                Daktar Eugenija imknait buvo labai domi, savita ir nepakartojama asmenyb,
                mokslinink ir ypa didel vaistini augal ir liaudies medicinos inov. Savo
                gyvenimu ir veikla sukaup ne tik didiul etnokultrin bei intelektualin turt
                Lietuvai, bet ir paliko neidildomus spdius su ja bendravusiems monms bei
                suformavo unikal savo, kaip liaudies iniuons vaizd. Gim 1920 m. kovo 11 d.
                Rusijoje, Krasnodaro krate, Novorosijsko mieste. Kartu su kitais pabgliais i
                Rusijos su eima grs  Lietuv tvas Pranas imknas gimtuosiuose Tauragnuose
                steig vaistin. Jau bdama eeri met, Eugenija imknait m pratintis prie
                vaistins darbo ir vaistaoli rinkimo. Nuo maens Eugenija buvo labai ingeidi.
                Turjo guv prot, viskuo domjosi, mgo klausytis pasakojim apie senov,
                paproius, olynus. Senos kaimo olininks ir kaimo senoliai, matydami Eugenijos
                pomgius, j mokino vaistini augal painimo ir paruoimo gydymui meno, ukeikim
                ir adjim, mok olynais gydyti mones, o ji dmiai vis klaussi. Baigusi
                Tauragn pradios mokykl pradjo mokytis Utenos gimnazijoje, buvo gabi, mokslai
                seksi. Eugenija sakydavo, kad jos tvai buvo labai protingi ir todl j aukldami
                nevert mokytis ko nors atmintinai: nei eilrai, nei daugybos lentels. Taip ji
                imoko visk mokytis apgalvodama, isiaikindama dalyko esm, o smegenyse liko
                vietos visiems jos gyvenime reikalingiems dalykams. stojusi  Kauno Vytauto
                Didiojo universiteto Medicinos fakulteto farmacijos skyri pasirinko tvo
                profesij, ruosi bti vaistininke. Karo pradioje dirbo Kauno, Tauragn
                vaistinse, 1942 m. atvyko  Vilni ir pradjo dirbti universiteto Botanikos sode.
                Praus karui botanikos sodas buvo sunaikintas, Eugenija dirbo Vilniaus 1-osios
                ligonins vaistinje, vliau perjo dirbti  universitet. 1949m. atvaiavo  Kaun
                ir dirboKauno botanikos sodejaunesnija moksline bendradarbe, msi disertacijos,
                augino ir tyr valerijon kultras. Kiek vliau mokslinio darbo tsti gro 
                Vilni,  Moksl akademijos Biologijos institut, dirbo Akademijos Botanikos sodo
                direktore. Apgynusi disertacij gavo biologijos moksl kandidats laipsn. 1955m.
                ivaiavo Kazachstandirbti vaistini augal kio Darmina padalinio stoties
                direktore, domjosi vidurins Azijos vaistiniais augalais, io krato liaudies
                medicina ir gyventoj paproiais, organizavo ekspedicijas laukini vaistini augal
                tyrimui. Grusi  Lietuv sidarbino vyriausioje farmacijos valdyboje vyr.
                inspektore vaistaoli paruooms, steigus atskir vaistaoli skyri paskirta jo
                virininke. Tuo metu jai suteikta aukiausia provizors farmacinio darbo
                organizators kvalifikacin kategorija. 1971 m. E. imknait Vilniaus universitete
                apgyn biologijos moksl daktars disertacij "Lietuvos vaistingj augal resurs
                naudojimo biologiniai pagrindai". Tai pirmoji ir turbt vienintel disertacija,
                kurioje nagrinjami Lietuvos vaistini augal resursai. 1993m. Lietuvos Mokslo
                Taryba E. imknaitei suteik habilituotos gamtos moksl daktars mokslin laipsn.
                Vis savo gyvenim dr. E. imknait paskyr vaistini augal, liaudies medicinos,
                kratotyros tyrimui. Para daug knyg, mokslini ir mokslo populiarinimo
                straipsni, skait praneimus mokslinse farmacinink ir botanik konferencijose,
                dalyvaudavo botanik ekspedicijose. Visuomenei skirt praneim klaussi pilnos
                auditorijos moni, kurie po kelias valandas jos nepaleisdavo, daug klausindavo.
                Tyrindama vaistaoli augimo vietas Eugenija imknait ivaikiojo Lietuv,
                surinko tkstanius odi lietuvi kalbos odynui. Domjosi ir rinko pasakas,
                padavimus, dainas ir sutartines. Ji buvo senovs mogus, jai gamta buvo
                neatskiriama gyvenimo dalis, ji suprato augal ir gyvn kalb, pasakos jai buvo
                ms protvi gyvenimo ir paproi iraika, senose dainose ji skait paslpt
                prasm, ms tv tikjimo ir gyvenimo atspind.
              </p>
              <div className="btn btn-read-more" onClick={this.readMoreClicked}>
                {this.state.readMoreClicked ? "SKAITYTI MAZIAU" : "SKAITYTI DAUGIAU"}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Page0;
